.Results {
  display: flex;
  flex-wrap: wrap;
}

.Results-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1 300px;
  box-sizing: border-box;
}

.Results-symbol {
  font-size: 4rem;

  .Results-image {
    width: 90%;
    border: 10px solid rgba(212, 153, 62);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    transition-duration: 0.2s;

    &:hover {
      border-width: 14px;
    }
  }
}

.Results-title {
  text-align: center;
}

button {
  height: 100%;
}