.Header {
  background-color: rgb(101, 22, 35);
  border-radius: 10px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 20px;
  left: 20px;
  right: 20px;
  z-index: 100;
  height: 90px;
}

.Header-title {
  padding-left: 30px;

  h1 {
    color: white;
    font-size: 2rem;
    font-weight: 700;
  }
}

.Header-button {
  display: flex;
  gap: 20px;

  button {
    transition-duration: 0.2s;
    font-size: 20px;
    font-weight: 700;
    background-color: rgb(225, 225, 225);
    color: black;
    border: none;
    border-radius: 5px;
    padding: 10px 20px 10px 20px;
    cursor: pointer;

    &:hover {
      background-color: #808080;
    }
  }
}

