@mixin list-p {
  padding: 0;
  margin: 0;
  color: black;
}

.List-container {
  margin-top: 30px;
}

.List-item {
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  padding: 20px;
  margin-bottom: 14px;
  background-color: rgb(234, 234, 234);
  transition-duration: 0.2s;

  &:hover {
    border: 6px solid #8d8d8d;
  }
  .List-thumbnail {
    width: 50px;
    height: 50px;
    margin-right: 10px;
    overflow: hidden;
  }
  
  .List-details {
    flex: 1;
    padding-left: 10px;

    .List-title {
      p {
        @include list-p;
        font-weight: 700;
      }
    }
    .List-origin {
      p {
        @include list-p;
      }
    }
  }
}

a {
  text-decoration: none;
}