.App {
    padding: 0 20px;
}

.filter-section {
    margin-top: 140px;
    display: flex;
    align-items: center;
    padding-bottom: 30px;

    button {
        transition-duration: 0.2s;
        font-size: 18px;
        font-weight: 400;
        background-color: rgb(101, 22, 35);
        color: #fff;
        border: none;
        border-radius: 5px;
        padding: 10px 20px 10px 20px;
        margin-left: 20px;
        cursor: pointer;

        &:hover {
            background-color: rgb(165, 73, 88);
        }
    }
}

.sort-dropdown {
    padding-top: 20px;
    display: flex;
    align-items: center;
  }
  
.sort-dropdown select {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    background-color: #fff;
    color: #333;
    font-size: 12px;
    cursor: pointer;
}

.filter-icon {
    font-size: 30px;
    color: rgb(59, 59, 59);
    padding-right: 10px;
}

.sort-icon {
    font-size: 30px;
    color: rgb(59, 59, 59);
    padding-right: 30px;
}